@font-face {
  font-family: "oswald";
  font-style: normal;
  font-weight: 400;
  src:
    url(../../fonts/oswald/oswaldregular.woff2) format("woff2"),
    url(../../fonts/oswald/oswaldregular.woff) format("woff");
  font-display: swap;
}

